.descriptionCard {
  display: flex;
  flex-direction: column;
}

.descriptionCard img {
  width: 100vw;
}

.descriptionCard div {
  margin-left: 16px;
  margin-bottom: 32px;
}

@media screen and (min-width: 768px) {
  .descriptionCard {
    flex-direction: row;
  }

  .descriptionCard img {
    width: 254px;
    margin-left: 16px;
  }
}

@media screen and (min-width: 1024px) {
  .descriptionCard {
    width: 1136px;
    margin: 160px;
    margin: 40px auto;
  }

  .descriptionCard img {
    width: 560px;
    margin-left: 16px;
  }

  .descriptionContainer {
    padding: 0 12vw;
  }
}
