/* Hero */

.hero img {
  width: 100%;
}

.hero aside {
  position: absolute;
  top: 35vw;
  left: 5vw;
}

.hero h2 {
  color: var(--write);
  font-size: 20px;
  margin: 12px 0;
}

.hero p {
  color: var(--write);
  font-size: 14px;
  margin: 12px 0;
}

.viewConsole {
  width: 118px;
  height: 40px;
  color: var(--write);
  font-weight: 600;
  border: none;
  background: var(--blue-100);
  display: block;
  text-decoration: none;
  text-align: center;
  padding: 8px;
}

@media screen and (min-width: 768px) {
  /* Hero */
  .hero aside {
    position: absolute;
    top: 27vh;
    left: 6vw;
  }

  .hero h2 {
    font-size: 48px;
  }

  .hero p {
    font-size: 22px;
  }
}

@media screen and (min-width: 1024px) {
  /* Hero */
  .hero img {
    height: 28vw;
    object-fit: cover;
  }

  .hero aside {
    top: 16vw;
  }
}

@media screen and (min-width: 1600px) {
  .hero aside {
    left: 16%;
  }
}
