:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --gray-200: #a2a2a2;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}

html {
  box-sizing: border-box;
  font-family: var(--font-family);
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

.AddNewProductContainer {
  background-color: var(--soft-purple);
  height: 500px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.AddNewProductContainer form {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.AddNewProductContainer h3 {
  margin-top: 16px;
  text-align: start;
}

.AddNewProductContainer input {
  width: 100%;
  height: 35px;
  padding-left: 6px;
  margin-bottom: 16px;
  border: none;
}

.AddNewProductContainer select {
  width: 100%;
  height: 35px;
  padding-left: 6px;
  margin-bottom: 16px;
  border: none;
  background-color: var(--write);
  -webkit-appearance: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.AddNewProductContainer .AddProductButton {
  background-color: var(--blue-100);
  margin: 8px auto;
  width: 100%;
  height: 51px;
  border: none;
  color: var(--write);
}

.AddNewProductContainer textarea {
  width: 100%;
  height: 61px;
  resize: none;
  border: none;
  margin-top: 0;
  margin-bottom: 10px;
}

.AddNewProductContainer p {
  font-size: 12px;
  color: var(--gray-200);
  font-family: var(--font-family);
  margin: 0;
  padding: 6px 6px 0;
  background-color: var(--write);
  text-align: start;
}

@media screen and (min-width: 1024px) {
  .AddNewProductContainer form {
    max-width: 559px;
  }
}
