:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}
.AllProductsContainer h1 {
  text-align: center;
}

.addProduct {
  background-color: var(--blue-100);
  display: block;
  margin: 8px auto;
  font-size: 15px;
  padding: 14px;
  width: 150px;
  text-align: center;
  text-decoration: none;
  border: none;
  color: var(--write);
}

.none {
  display: none;
}

@media screen and (min-width: 768px) {
  .AllProductsContainer section {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1024px) {
  .AllProductsContainer section {
    width: 60%;
  }
}
