:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}

@media screen and (min-width: 1024px) {
  .consolesContainer {
    max-width: 90%;
    margin: auto;
  }
}

@media screen and (min-width: 1600px) {
  .consolesContainer {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}
