:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}

.LoginContainer {
  background-color: var(--soft-purple);
  height: 500px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.LoginContainer h3 {
  margin-top: 125px;
}

.LoginContainer input {
  margin: 8px auto;
  padding: 14px;
  width: 260px;
  border: none;
  color: var(--font-color);
  border-radius: 4px;
}
