/* Contact Form */
.none {
  display: none;
}

.contactForm {
  max-width: 434px;
  max-height: 286px;
  text-align: center;
}

.contactForm h3 {
  padding: 0;
  margin: 20px 0 0 0;
}
.contactForm input {
  width: 39.4%;
  margin: 16px 5px;
  border-radius: 4px;
  border: none;
  font-family: var(--font-family);
  padding: 6px;
}

.contactForm textarea {
  width: 80%;
  max-width: 434px;
  height: 150px;
  margin: 0px auto;
  resize: none;
  font-family: var(--font-family);
  border-radius: 4px;
  border: none;
  font-size: 14px;
}

.btn {
  width: 118px;
  height: 40px;
  margin: 16px auto;
  color: var(--write);
  font-weight: 600;
  border: none;
  background: var(--blue-100);
}

.contactForm p {
  position: relative;
  bottom: 200px;
  font-size: 18px;
  background: var(--blue-100);
  margin: 0 auto;
  width: 118px;
  height: 40px;
  padding: 8px;
  color: var(--write);
}

@media screen and (min-width: 1024px) {
  .contactForm {
    margin-right: auto;
  }
}

@media screen and (min-width: 1600px) {
  .contactForm {
    margin-left: 0px;
    margin-right: 100px;
  }

  .contactForm textarea {
    max-width: 100%;
  }
}
