:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}

/* AluraContainer */
.aluraGeekContainer {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  background-color: var(--soft-purple);
  padding-bottom: 45px;
}

.aluraGeek {
  max-width: 254px;
  max-height: 300px;
  text-align: center;
  margin: 16px auto;
  display: block;
  text-decoration: none;
  color: var(--font-color);
}

.aluraGeek img {
  width: 176px;
  height: 50px;
}

@media screen and (min-width: 768px) {
  /* AluraContainer */
  .aluraGeekContainer {
    flex-direction: row;
  }
}

@media screen and (min-width: 1024px) {
  /* AluraContainer */
  .contactForm {
    margin: 0 12.5vw;
  }
}

@media screen and (min-width: 1600px) {
  .contactForm {
    margin: 0 15vw;
  }
}
