:root {
  --blue-100: #2a7ae4;
  --soft-purple: #eaf2fd;
  --write: #ffffff;
  --gray-100: #e5e5e5;
  --font-color: #464646;
  --font-family: "Inter", sans-serif;
}

html {
  box-sizing: border-box;
  font-family: var(--font-family);
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Header */
nav {
  display: flex;
  justify-content: space-between;
  height: 72px;
  width: 100%;
  background-color: var(--write);
  position: sticky;
  top: 0;
  z-index: 9999;
}

.logo {
  width: 100px;
  height: 28px;
  margin: 22px 0 22px 15px;
}

.login {
  margin: 1rem auto;
  padding: 9px 16px;
  width: 133px;
  height: 40px;
  border: 1px solid var(--blue-100);
  color: var(--blue-100);
  font-size: 14px;
  font-weight: 600;
  background-color: var(--write);
  text-align: center;
  text-decoration: none;
}

/* Search Btn */

.searchContainer {
  position: relative;
  padding: 16px;
}

.searchContainer input {
  width: 40px;
  height: 40px;
  padding: 0 20px;
  font-size: 18px;
  color: #222;
  outline: none;
  border: none;
  border-radius: 30px;
  transition: all 0.6s ease;
  background-color: var(--soft-purple);
}

.searchBtn {
  width: 40px;
  height: 40px;
  line-height: 55px;
  top: 12px;
  right: 15px;
  position: absolute;
  text-align: center;
  color: white;
  font-size: 18px;
  border-radius: 50%;
  cursor: pointer;
}

.searchContainer:hover input {
  width: 220px;
}

.searchContainer input:focus {
  width: 220px;
}

.navLinkContainer {
  color: var(--font-color);
  text-decoration: none;
}

.navLinkContainer p {
  background: var(--soft-purple);
  margin: 0 15px;
  padding: 10px;
  border: 1px solid var(--font-color);
}

/* .navLinkContainer:nth-last-child(-n + 15) {
  display: none;
} */

@media screen and (min-width: 1024px) {
  /* Header */
  .nav {
    height: 115px;
  }

  .logo {
    width: 176px;
    height: 50px;
    margin-left: 6vw;
    margin-top: 11px;
    margin-bottom: 11px;
  }

  .login {
    order: 1;
    margin-right: 6vw;
    width: 182px;
    height: 50px;
    margin-top: 11px;
    margin-bottom: 11px;
    padding: 13px 16px;
  }

  .searchBtn {
    order: 0;
  }

  /* Search Btn */

  .searchContainer input {
    width: 393px;
  }

  .searchContainer:hover input {
    width: 393px;
  }

  .searchContainer input:focus {
    width: 393px;
  }
}

@media screen and (min-width: 1600px) {
  nav {
    width: 100%;
    padding: 0 10%;
  }
}
