/* The Shop Counter */
.seeEverything {
  height: 16px;
  border: none;
  color: var(--blue-100);
  font-size: 14px;
  font-weight: 600;
  background-color: var(--write);
  margin: auto 0;
  text-decoration: none;
  text-align: center;
}

.headerTheShopCounter {
  display: flex;
  justify-content: space-between;
  margin: auto 16px;
}

/* Product container */
.productContainer {
  display: flex;
  flex-wrap: wrap;
  width: 344px;
  margin: auto;
}

.centeringProductContainer {
  width: fit-content;
  margin: auto !important;
}

.product:nth-child(-n + 2) {
  display: none;
}

.seeAllProducts .product {
  display: block;
}

.seeAllProductsLessOne:nth-child(-n + 1) {
  display: none;
}

.none {
  display: none;
}

/* Product */
.product {
  width: 156px;
  height: 249px;
  margin: 16px 8px;
  position: relative;
}

.product img {
  width: 156px;
  height: 156px;
}

.product p {
  font-size: 14px;
  margin: 0 auto;
}

.product h2 {
  font-size: 16px;
  margin: 6px auto;
}

.seeProduct {
  height: 16px;
  border: none;
  color: var(--blue-100);
  font-size: 14px;
  font-weight: 600;
  background-color: var(--write);
  margin: auto 0;
  text-decoration: none;
  text-align: center;
  padding: 0px;
}

.product .inputEditAndDelete {
  display: block;
  position: absolute;
  top: 20px;
  right: 8px;
}

.product .inputEditAndDelete img {
  height: 24px;
  width: 24px;
  margin-left: 5px;
}

@media screen and (min-width: 768px) {
  .productContainer {
    width: 688px;
  }
}

@media screen and (min-width: 1024px) {
  /* Product container */
  .theShopCounter {
    max-width: 90%;
    margin: auto;
  }
}

@media screen and (min-width: 1600px) {
  .productContainer {
    margin-bottom: 64px;
    width: 1152px;
  }

  .theShopCounter {
    max-width: 70%;
    margin: auto;
  }
  /* Product */
  .isActive {
    display: inline;
  }

  .product {
    width: 176px;
  }

  .product img {
    width: 176px;
    height: 176px;
  }

  .product:nth-child(-n + 2) {
    display: block;
  }

  .product .inputEditAndDelete {
    top: 10px;
    right: -8px;
  }
}
